<template>
  <div class="cms-list-layout" >
    <cms-prompt collapse='true'>
      <li>根据微信平台规定、订阅号提供每天可支持一条群发，服务号提供每自然月提供4条群发</li>
      <li> 系统判定每月或每日发送次数依据您接入本系统开始的数据，如您之前已在微信平台或其他第三方系统发送过，依然能在本系统中进行设置，但可能会导致群发失败，只需等待发送次数重置之后恢复正常</li>
      <li>目前仅支持本月且当日之后的日期进行定时群发设置</li>
    </cms-prompt>
    <calendar class="ui-calendar" v-loading="loading"
    :disabled-days-of-week="disabled"
     :start-day="currMonth" :dateData="dateData" :mode="mode" locale="zh-cn" 
     :render-header="renderHeader" ref="calendar">
      <div :class="[
            'ui-calendar-item',
            {
              'is-otherMonth': checkedTime(item)||item.isPrevMonth || item.isNextMonth ,
              'is-today': item.isToday
            },
          ]" slot-scope="item" @click="setMessage(item)">
        <div :class="['ui-calendar-item-date']">
          {{item.date.date}}
        </div>
        <!-- 这里自定义显示 -->
        <div v-if="item.data.length>0" class="ui-calendar__thumb">
          <span class="ui-calendar__type">{{item.data[0].type}}</span>
          <div  class="ui-cover__warpper">
            <img class="t-img" v-if="item.data[0].url" :src="$cms.weChatUrl(item.data[0].url)" alt="">
            <img class="t-img" v-if="item.data[0].mediaData.mediaType === 'video'" src="./../../../../assets/images/video2.png" />
            <img class="t-img" v-if="item.data[0].mediaData.mediaType === 'voice'" src="./../../../../assets/images/voice.png" />
          </div>
        </div>
        <div v-if="item.data.length>0" class="edit-or-del">
           <span v-if="!checkedTime(item)" class="el-icon-edit" @click.stop @click="editItem(item)" ></span>
           <span  class="el-icon-delete" @click="delItem(item)"></span>
          </div>
      </div>
    </calendar>
    <el-dialog :visible="visible" title="群发设置" width="750px" :before-close="close">
      <el-form ref="form" :rules="rules" :model="formData" label-width="120px">
        <el-form-item label="接收对象" prop="tagId">
          <el-select v-model="formData.tagId">
            <el-option label="全部用户" value=""></el-option>
            <el-option v-for="item in fansTags" :key="item.id" :label="item.tagName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="定时发送时间" prop="sendHour">
          <el-time-select
            v-model="formData.sendHour"
            :picker-options="{
              start: '08:00',
              step: '01:00',
              end: '22:00'
            }"
            placeholder="选择发送时间">
          </el-time-select>
        </el-form-item>
        <el-form-item prop="materialId">
          <media-warpper @change="getMediaData" :data="formData.mediaData"></media-warpper>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer center">
        <cms-button v-if="type === 'add'" type="primary" @click="saveMessage">确 定</cms-button>
        <cms-button v-if="type === 'update'" type="primary" @click="updateMessage">修 改</cms-button>
        <cms-button type='info' @click="close">取 消</cms-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  wechatSendList,
  weChatFansTags,
  wechatSendSave,
  wechatSendDel,
  wechatSendUpdate
} from './../../../../api/wechat/index.js'
import calendar from 'vue2-event-calendar'
import rules from '@/rules/index.js'
import mediaWarpper from './../materialLibrary/components/mediaWarpper'
var defaultForm = {
  sendDate: '',
  sendHour: '',
  tagId: '',
  materialId: ''
}
export default {
  name: 'weChatMass',
  components: { calendar, mediaWarpper },
  data() {
    return {
      type: 'add',
      currMonth: '',
      dateData: [],
      fansTags: [],
      mode: 'month',
      visible: false,
      formData: Object.assign({}, defaultForm),
      startTime: '08:00:00-22:00:00',
      rules: {
        'sendHour': [rules.required('请选择发送时间', 'change')],
        'materialId': [rules.required('请选择发送素材')]
      },
      disabled: [],
      mediaData: null,
      loading: false
    }
  },
  methods: {
    editItem(item) { // 修改当前
      // this.mediaData = item.data[0].mediaData
      this.visible = true
      this.formData = {
        'id': item.data[0].id,
        'sendDate': item.data[0].date,
        'sendHour': item.data[0].sendHour,
        'tagId': item.data[0].tagId,
        'materialId': item.data[0].materialId,
        mediaData: item.data[0].mediaData
      }
      this.type = 'update'
    },
    delItem(item) { // 删除当前
      this.$confirm(
        '是否要将此定时任务删除',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          wechatSendDel({ ids: [item.data[0].id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('删除成功')
              this.getData()
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    saveMessage(items) {
      // 保存设置定时群发
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.formData.sendHour = parseInt(this.formData.sendHour)
          wechatSendSave(this.formData).then(res => {
            if (res.code === 200) {
              this.$successMessage('设置成功')
              this.visible = false
              this.close()
              this.getData()
            } else {
              this.visible = false
              this.close()
              this.$errorMessage(res.message)
            }
          })
        }
      })
    },
    updateMessage(items) {
      delete this.formData.mediaData
      // 修改设置定时群发
      this.formData.sendHour = parseInt(this.formData.sendHour)
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          wechatSendUpdate(this.formData).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
              this.close()
              this.getData()
            } else {
              this.close()
              this.$errorMessage(res.message)
            }
          })
        }
      })
    },
    getMediaData(items) {
      this.formData.materialId = items[0].id
    },
    getData() {
      this.loading = true
      this.today = this.$moment(new Date().getTime(), 'YYYY-MM')
      // this.startTime = this.$moment(new Date().getTime(), 'HH:mm:ss') + '-22:00:00'
      wechatSendList({ date: this.today }).then(res => {
        if (res.code === 200 && res.data.length > 0) {
          const data = []
          const typeNames = {
            news: '图文',
            image: '图片',
            voice: '语音',
            video: '视频'
          }
          res.data.forEach(v => {
            const params = {
              date: this.$moment(v.sendDate, 'YYYY-MM-DD'),
              id: v.id,
              sendHour: v.sendHour + '.00',
              materialId: v.wechatMaterial.id,
              tagId: v.tagId,
              type: typeNames[v.wechatMaterial.mediaType],
              url: '',
              mediaData: v.wechatMaterial
            }
            if (params.tagId === undefined) {
              params.tagId = ''
            }
            if (v.wechatMaterial.mediaType === 'news') {
              params.url = v.wechatMaterial.requestArray[0].thumbMediaUrl
              // params.url = this.$cms.weChatUrl(
              //   v.wechatMaterial.requestArray[0].thumbMediaUrl
              // )
            }
            if (v.wechatMaterial.mediaType === 'image') {
              params.url = v.wechatMaterial.request.url
              // params.url = this.$cms.weChatUrl(v.wechatMaterial.request.url)
            }
            if (v.wechatMaterial.mediaType === 'video') {
              params.url = null
            }
            if (v.wechatMaterial.mediaType === 'voice') {
              params.url = null
            }
            data.push(params)
          })
          this.dateData = data
        } else {
          this.dateData = []
        }
        this.loading = false
      })
    },
    checkedTime(item) {
      var toMouth = this.$moment(new Date().getTime(), 'YYYY-MM') === item.date.full.split('-').splice(0, 2).join('-')
      // 判断时间
      let nowDay = this.$moment(new Date().getTime(), 'YYYY-MM-DD').replace(/-/g, '/')
      nowDay = new Date(nowDay).getTime()
      let checkDay = item.date.full.replace(/-/g, '/')
      checkDay = new Date(checkDay).getTime()
      if (nowDay - checkDay >= 0 || !toMouth) {
        return true
      } else {
        return false
      }
    },
    setMessage(item) {
      this.formData.mediaData = {}
      this.type = 'add'
      // 显示操作面板
      if ((item.isNextMonth || item.isPrevMonth || this.checkedTime(item) || item.isToday) && item.data.length === 0) {
        this.$successMessage('只能选择当前月当日之后的时间')
      } else {
        if (item.data.length > 0) {
          this.mediaData = item.data[0]
        } else {
          this.formData.sendDate = item.date.full
          this.visible = true
        }
      }
    },
    renderHeader({ prev, next, selectedDate }) {
      const h = this.$createElement
      const nextButton = h(
        'div',
        {
          class: ['ui-calendar-modeBtn'],
          on: {
            click: prev
          }
        },
        ['<']
      )
      const dateText = h('div', { class: ['ui-calendar-date'] }, [
        selectedDate
      ])
      const prevButton = h(
        'div',
        {
          class: ['ui-calendar-modeBtn'],
          on: {
            click: next
          }
        },
        ['>']
      )
      return h(
        'div',
        {
          class: 'calendar-header__warpper'
        },
        [nextButton, dateText, prevButton]
      )
    },
    close() {
      this.mediaData = null
      this.visible = false
      this.$refs.form.clearValidate()
      this.formData = Object.assign({}, defaultForm)
    },
    getfansTags() {
      weChatFansTags().then(res => {
        this.fansTags = res.data.tagList
      })
    }
  },
  created() {
    this.getfansTags()
    this.getData()
  }
}
</script>

<style  lang="scss">
.cms-layout .el-form-item__error{
  top: 10px;
}
.media-warpper{
  margin: 0 !important;
  margin-right: 15px;
}
.ui-cover__warpper{
position: relative;
width: 100%;
height: 50px;
overflow: hidden;
}
.ui-calendar__thumb {
  position: relative;
  width: 100%;
  height: 73px;
  text-align: center;
}
.ui-calendar__type{
  position: absolute;
  background: rgba(0, 0, 0, .7);
  color: #fff;
  font-size: 12px;
  padding: 2px 8px;
  right: 10px;
  top:-26px;
}

.calendar-header__warpper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.ui-calendar-modeBtn {
  width: 50px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 16px;
  cursor: pointer;
  &:hover {
    color: #19a0ff;
  }
}
.ui-calendar-date {
  font-size: 16px;
}
.is-otherMonth {
  color: #c0c4cc;
  cursor: not-allowed;
}
.ui-calendar-item {
  height: 100%;
  position: relative;
}
.ui-calendar-item-date {
 padding: 5px 10px;
  font-size: 14px;
}

.vue-calendar-header {
  position: relative;
  padding: 10px;
  display: flex;
  align-items: center;
}
.vue-calendar-header-center {
  flex: 1;
  text-align: center;
  font-size: 14px;
  color: #19a0ff;
}
.vue-calendar-header-left,
.vue-calendar-header-right {
  flex: 1;
}
.vue-calendar-header-date {
  display: inline-block;
  margin: 0 32px;
}
.vue-calendar-control {
  cursor: pointer;
}

.vue-calendar-body {
  display: flex;
  flex-direction: column;
  flex: 1;
  border-right: 1px solid #e8ebee;
}
.vue-calendar-body-row {
  flex: 1;
  display: flex;
  height: 105px;
}
.vue-calendar-day-item {
  flex: 1;
  overflow: hidden;
  border-color: #e8ebee;
  border-style: solid;
  border-width: 0 0 1px 1px;
  cursor: pointer;
}
.vue-calendar {
  background: #fff;
  display: flex;
  flex-direction: column;
}

.vue-calendar-header-left,
.vue-calendar-header-right {
  flex: 1;
}
.vue-calendar-header-date {
  display: inline-block;
  margin: 0 32px;
}
.vue-calendar-control {
  cursor: pointer;
}
.vue-calendar-week-title {
  display: flex;
  border-color: #e8ebee;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
}
.vue-calendar-week-title-item {
  flex: 1;
  height: 30px;
  line-height: 30px;
  padding: 0 15px;
  color: #19a0ff;
}
.edit-or-del{
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 5px;
  cursor: pointer;
}
.t-img{
  width:auto;
  height:auto;
  line-height: 50px;
  max-width:100%;
  max-height:80%;
}
</style>



